import { Button, Modal, Form } from "react-bootstrap";
import { Link } from "react-router-dom";
import { useState } from "react";
import "./index.css";

export function ModalUser() {
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  return (
    <>
      <Button variant="primary" onClick={handleShow}>
        <strong>➥</strong> Вход{" "}
      </Button>

      <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>Вход в личный кабинет</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form>
            <Form.Group className="mb-3 " controlId="exampleForm.ControlInput1">
              <Form.Label>Введите номер телефона</Form.Label>
              <Form.Control className="inputModal" type="tel" value="+7" />
            </Form.Group>
            <Form.Group className="mb-3" controlId="exampleForm.ControlInput2">
              <Form.Label>Введите код из смс</Form.Label>
              <Form.Control className="inputModal" type="text" />
            </Form.Group>
            <Form.Check type="checkbox" id={`check-api-checkbox`}>
              <Form.Check.Input type="checkbox" isValid />
              <Form.Check.Label className='checkBox'>
                Указывая номер телефона я принимаю{" "}
                <Link to="/" className="link">условия пользовательского соглашения</Link>
              </Form.Check.Label>
            </Form.Check>
          </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Войти
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
}
